@import "../../common/sass/sass";

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  @media screen and (max-width: $medium){
    width: 100%;
    min-height: 100vh;
  }
}